      <h4 class="fw-bold">So war....</h4>

      <p>Łukasz Zalesiński "Be ruthless, be brutal" - appealed to his commanders Adolf Hitler. On September 1, 1939, at dawn, the German war machine attacked Poland, starting a war that opened a completely new era in the history of the world. The Poles lost the first match, but managed to inflict significant losses on the opponent. Punctually at 6:30, the Polish Radio program was interrupted, and a message read out by the announcer Józef Małgorzewski was broadcast into the air. So war! As of today, all matters and issues are relegated to the background. We shift our entire public and private life onto a special track […]. All the efforts of the nation must go in one direction. We are all soldiers. We only have to think about one thing - fight to victory! ”. It was Friday, September 1, 1939. Less than two hours earlier, the first missiles fired from the battleship "Schleswig-Holstein" fell on the Westerplatte peninsula, where the Polish military depot was located, Luftwaffe planes had already turned the border city of Wieluń into ashes, and the armored Wehrmacht forces struck several Polish armies with force. On this clear morning, the history of the world took a whole new turn. It happened what probably everyone had been expecting for the last few months.
