      <h4 class="fw-bold">Excuse from the head
</h4>
      <p>Adolf Hitler wanted war. When he took power six years earlier, he set a clear goal: to change the Versailles order, rebuild the power of Germany, redraw the map of Europe - without Jews and with a trace number of Slavs employed as slaves. He pursued his goal in small steps, testing the patience and determination of the Western allies. And they allowed him to do almost everything. Contrary to what was agreed in Versailles, the German army grew swollen, overgrowing with modern armored forces, aviation and navy. In 1936, Hitler captured the demilitarized Nadrenia. Two years later, he incorporated Austria into the Reich. He also subjugated Czechoslovakia through diplomatic games and military pressure. Poland became the next destination. In October 1938, the Reich Minister of Foreign Affairs, Joachim von Ribbentrop, made a proposal to the Polish ambassador Józef Lipski concerning, inter alia, the incorporation of the Free City of Gdańsk into Germany and the construction of extraterritorial motorways and railroads that would intersect the so-called Polish Corridor. In return, he offered, among others extension of the non-aggression pact by 25 years and a guarantee of the sale of Polish goods in Gdańsk. In the spring of 1939, Hitler himself sent a memorandum to the government in Warsaw. The Poles, however, were already sure: the Reich is playing a game that is at best aimed at making its neighbor vassal. They said no. - Poland cannot be pushed away from the Baltic Sea! - the head of the Polish Ministry of Foreign Affairs, Józef Beck, thundered with the expose of the Sejm. The preparations for the war were in full swing. the incorporation of the Free City of Gdańsk into Germany and the construction of extraterritorial motorways and railroads that would intersect the so-called Polish Corridor. In return, he offered, among others extension of the non-aggression pact by 25 years and a guarantee of the sale of Polish goods in Gdańsk. In the spring of 1939, Hitler himself sent a memorandum to the government in Warsaw. However, the Poles were already sure: the Reich is playing a game that is at best aimed at making its neighbor vassal. They said no. - Poland cannot be pushed away from the Baltic Sea! - the head of the Polish Ministry of Foreign Affairs, Józef Beck, thundered with the expose of the Sejm. Preparations for the war were in full swing. the incorporation of the Free City of Gdańsk into Germany and the construction of extraterritorial motorways and railway lines that would intersect the so-called Polish Corridor. In return, he offered, among others extension of the non-aggression pact by 25 years and a guarantee of the sale of Polish goods in Gdańsk. In the spring of 1939, Hitler himself sent a memorandum to the government in Warsaw. However, the Poles were already sure: the Reich is playing a game that is at best aimed at making its neighbor vassal. They said no. - Poland cannot be pushed away from the Baltic Sea! - the head of the Polish Ministry of Foreign Affairs, Józef Beck, thundered with the expose of the Sejm. Preparations for the war were in full swing. In the spring of 1939, Hitler himself sent a memorandum to the government in Warsaw. However, the Poles were already sure: the Reich is playing a game that is at best aimed at making its neighbor vassal. They said no. - Poland cannot be pushed away from the Baltic Sea! - the head of the Polish Ministry of Foreign Affairs, Józef Beck, thundered with the expose of the Sejm. Preparations for the war were in full swing. In the spring of 1939, Hitler himself sent a memorandum to the government in Warsaw. However, the Poles were already sure: the Reich is playing a game that is at best aimed at making its neighbor vassal. They said no. - Poland cannot be pushed away from the Baltic Sea! - the head of the Polish Ministry of Foreign Affairs, Józef Beck, thundered with the expose of the Sejm. The preparations for the war were in full swing.
        <br><br>The plan for the German invasion was codenamed Fall Weiss. The Wehrmacht was to strike from the north and west, as well as the Reich-controlled territories of Bohemia and Slovakia. “Destroying Poland is our first task. The goal must not be to reach some marked line, but to destroy a living force. Be ruthless, be brutal, "Hitler urged his commanders during a conference in the last days of August, quietly negotiating with the Soviet Union, which was to lend him military support.
<br><br>
        Polish military, in turn, from March feverishly worked on the "Operational Plan West". For them, the prospect of a war with Germany was something new. For years, they considered the Red Army a far greater threat. Now the planners assumed that the first stage of the fighting would take place along the borders. If the aggressor fails to stop, the Poles will defend themselves on the line of the Vistula. The key issue in this puzzle, however, was the attitude of the allies. The Polish authorities hoped that a few days after the German aggression, France and Great Britain would join the fight, and the Wehrmacht, taken in a pinch, would surrender. At the time, the decision-makers did not know that the Red Army was also preparing to attack Poland ... In the summer, the Germans began intensified subversive activities. They attacked border posts, arranged a bomb attack at the Tarnów railway station, attempted to occupy a strategic tunnel on the Jabłonkowska Pass. The culmination of a series of incidents was the Gliwice provocation, i.e. a raid of an armed commando to a radio station, which was located ... on the German side of the border. The Nazis wanted to blame the Poles for the trip. Although the action was carried out extremely ineptly, several dozen hours later Hitler listed it as "casus belli". In line with his credo, which he laid out to his commanders at the aforementioned conference: “For the purposes of propaganda, I will give some reason for the outbreak of the war, never mind whether it will be credible or not. Nobody asks the winner whether they told the truth or not. In matters related to starting and waging a war, the decisive factor is not the law, but the victory ”. that is, a rally of an armed commando to a radio station that was located ... on the German side of the border. The Nazis wanted to blame the Poles for the trip. Although the action was carried out extremely ineptly, several dozen hours later Hitler listed it as "casus belli". In line wi