      <h4 class="fw-bold"> Five weeks of fighting</h4>
      <p>On August 25, the battleship "Schleswig-Holstein" entered the port in Gdańsk. Officially, its crew was to take part in the ceremonies commemorating the sailors of the cruiser "Magdeburg", which sank during the First World War. In fact, the ship was preparing to attack the Polish depot at Westerplatte. Before the first volleys were fired, however, the Germans invited local decision-makers on board in accordance with a diplomatic ceremony. “A glass of champagne to the accompaniment of mocking smiles of snotty officers. It was the most difficult moment in my life, ”noted Marian Chodacki, secretary general of the Republic of Poland in Gdańsk, in the daily. At 4:45 a.m. on September 1, the noise of the ships' guns broke the silence of the morning. Most historians maintain that this was where World War II began.<br><br>
        To fight against its eastern neighbor, the Germans threw a total of 1.8 million soldiers, 2.8 thousand tanks and 1.3 thousand aircraft. Poland could oppose them less than a million soldiers, 880 tanks and 400 aircraft. The strategic location of the Republic of Poland was extremely unfavorable. The borders with the territories controlled by the Reich stretched for nearly three thousand kilometers, in addition, in most places there were no natural obstacles and fortifications. Mobile and heavily armed Wehrmacht troops in the first three days won the border war, breaking the enemy's defenses and pushing his armies deep into the country. Hope was poured into the hearts of Poles by the decision of the governments of France and Great Britain. On September 3, in line with their allied commitments, they declared war on Germany. However, the expected offensive on the Western Front did not take place. Reason? The Allies decided they were not yet ready to face the Nazi war machine. However, not all of their commanders shared this opinion. “You could enter German positions like butter. It was possible to settle the war in 1939. All it took was a bit of determination and character - lamented French Marshal Alphonse Juin years later. - What a disgrace, what a shame, what a stupidity at the same time. This is an ideal situation, even a model, in which you could beat your opponent by going to his rear. " All it took was a bit of determination and character - lamented the French marshal Alphonse Juin years later. - What a disgrace, what a shame, what a stupidity at the same time. This is an ideal situation, even a model, in which you could beat your opponent by going to his rear. " All it took was a bit of determination and character - lamented French Marshal Alphonse Juin years later. - What a disgrace, what a shame, what a stupidity at the same time. This is an ideal situation, even a model, in which you could beat your opponent by going to his rear. "<br>
        Meanwhile, despite the growing advantage of the opponent, the Poles fought bravely. On the night of September 9-10, they even managed to deliver an effective strike. The armies "Poznań" and "Pomerania", commanded by General Tadeusz Kutreb, delivered an unexpected blow to the wing of the 8th Army of General Johannes Blaskowitz. The bloody battle on the Bzura stopped the German attack on Warsaw for several dozen hours. Polish hopes were finally buried on September 17, when the Red Army attacked from the east. For example, Warsaw fell at the end of September, the Fortified Hel Region surrendered on October 2, and the Independent Operational Group Polesie commanded by General Franciszek Kleeberg four days later. The defensive war was lost and Poland disappeared from the map of Europe.</p>
      <h4 class="fw-bold">This is just the beginning...</h4>
      <p>The losses were huge. Many cities were ruined, many villages were set on fire. The Germans and the Soviets showed that in order to achieve military and ideological goals, they are ready to break all the rules of war. Civilians were regularly killed. It is enough to recall Wieluń - at the time of the raid, there was not a single soldier in the town, Polish troops were stationed several dozen kilometers away. One of the witnesses of the raid recalled “The damage was huge (…). Next to the hospital, I saw a corpse hanging on the branches of a tree. There were many killed and wounded in the streets (...). The panic was indescribable. People fled the city, many ran only in their underwear. The surprise was so great that some people went mad. " 66,000 Polish soldiers were killed in the fighting, and nearly 700,000 were taken prisoner. But the Poles were also able to inflict heavy losses on the attackers, especially the Germans. The Blitzkrieg planned by the Nazi commanders turned into a heavy fighting lasting over a month. During the September campaign, the Wehrmacht lost over 17,000 soldiers, 217 tanks, over ten thousand different types of vehicles and huge stocks of ammunition. As a result, they had to shift the invasion to Western Europe. The late prof. Paweł Wieczorkiewicz noted: "Hitler, assessing the conclusions of the campaign, stated that" if Poland had anti-tank weapons, the victorious march would not be possible ". Indeed, tank wedges as well as aviation played a decisive role in this. The overwhelming material advantage of the Wehrmacht (...) was not so much the quality of the equipment (...) as its quantity and the doctrine of massive use ”. In the historian's opinion, during the defensive fights, contrary to the opinions disseminated later, the small Polish air force and cavalry did quite well. “Mobile and well equipped with anti-tank guns, it was in fact the most effective means of stopping the progress of German tanks. In the later stages of the campaign, cavalry units were able to elude the enemy more effectively and longer than infantry who were much more physically weary of retreating marches. "<br>
        After defeating Poland, Hitler turned to France and Great Britain with an offer of peace. The Allies, however, rejected her. The war was just beginning, and Polish soldiers were to mark their presence on almost all fronts.
      </p>
