    <div v-if="lang===`it`">
      <h4 class="fw-bold">Quindi guerra...</h4>

      <p>Łukasz Zalesiński "Sii spietato, sii brutale" - ha fatto appello ai suoi comandanti Adolf Hitler. Il 1 settembre 1939, all'alba, la macchina da guerra tedesca attaccò la Polonia, dando inizio a una guerra che aprì un'era completamente nuova nella storia del mondo. I polacchi hanno perso la prima partita, ma sono riusciti a infliggere perdite significative all'avversario. Puntualmente alle 18:30, il programma della radio polacca è stato interrotto e nell'aria è stato trasmesso un messaggio letto dall'annunciatore Józef Małgorzewski. Quindi guerra! Ad oggi, tutte le questioni e le questioni sono relegate in secondo piano. Spostiamo tutta la nostra vita pubblica e privata su un binario speciale […]. Tutti gli sforzi della nazione devono andare in una direzione. Siamo tutti soldati. Dobbiamo solo pensare a una cosa: combattere per la vittoria! ”. Era venerdì 1 settembre 1939. Meno di due ore prima, i primi missili lanciati dalla corazzata "Schleswig-Holstein" sono caduti sulla penisola di Westerplatte, dove si trovava il deposito militare polacco, gli aerei della Luftwaffe avevano già ridotto in cenere la città di confine di Wieluń e le forze corazzate della Wehrmacht colpì con la forza diversi eserciti polacchi. In questa mattina limpida, la storia del mondo ha preso una svolta completamente nuova. È successo quello che probabilmente tutti si aspettavano negli ultimi mesi.
      </p>
      <h4 class="fw-bold">Scusa dalla testa</h4>
      <p>Adolf Hitler voleva la guerra. Quando ha preso il potere sei anni prima, si è posto un obiettivo chiaro: cambiare l'ordine di Versailles, ricostruire il potere della Germania, ridisegnare la mappa dell'Europa - senza ebrei e con una traccia di slavi gettati come schiavi. Ha perseguito il suo obiettivo a piccoli passi, mettendo alla prova la pazienza e la determinazione degli alleati occidentali. E gli hanno permesso di fare quasi tutto. Contrariamente a quanto concordato a Versailles, l'esercito tedesco si gonfiò, invadendo le moderne forze corazzate, l'aviazione e la marina. Nel 1936, Hitler catturò la Nadrenia smilitarizzata. Due anni dopo, incorporò l'Austria nel Reich. Ha anche soggiogato la Cecoslovacchia attraverso giochi diplomatici e pressioni militari. La Polonia è diventata la prossima destinazione. Nell'ottobre 1938, il ministro degli Affari esteri del Reich, Joachim von Ribbentrop, fece una proposta all'ambasciatore polacco Józef Lipski riguardo, tra l'altro, l'incorporazione della Città Libera di Danzica in Germania e la costruzione di autostrade e ferrovie extraterritoriali che intersecassero la cosiddetta Corridoio polacco. In cambio, ha offerto, tra gli altri proroga di 25 anni del patto di non aggressione e garanzia della vendita di merci polacche a Danzica. Nella primavera del 1939, lo stesso Hitler inviò un memorandum al governo di Varsavia. I polacchi, però, ne erano già sicuri: il Reich sta giocando un gioco che al meglio mira a rendere vassallo il suo vicino. Hanno detto di no. - La Polonia non può essere spinta via dal Mar Baltico! - tuonò il capo del ministero degli Affari esteri polacco, Józef Beck, con la denuncia del Sejm. I preparativi per la guerra erano in pieno svolgimento. l'incorporazione della Città Libera di Danzica in Germania e la costruzione di autostrade e ferrovie extraterritoriali che intersecassero la cosiddetta Corridoio polacco. In cambio, ha offerto, tra gli altri proroga di 25 anni del patto di non aggressione e garanzia della vendita di merci polacche a Danzica. Nella primavera del 1939, lo stesso Hitler inviò un memorandum al governo di Varsavia. I polacchi, però, erano già sicuri: il Reich sta giocando un gioco che al meglio mira a rendere vassallo il suo vicino. Hanno detto di no. - La Polonia non può essere spinta via dal Mar Baltico! - il capo del ministero degli Affari esteri polacco, Józef Beck, tuonò con un'esposizione. I preparativi per la guerra erano in pieno svolgimento. l'incorporazione della Città Libera di Danzica in Germania e la costruzione di autostrade e ferrovie extraterritoriali che intersecassero la cosiddetta Corridoio polacco. In cambio, ha offerto, tra gli altri proroga di 25 anni del patto di non aggressione e garanzia della vendita di merci polacche a Danzica. Nella primavera del 1939, lo stesso Hitler inviò un memorandum al governo di Varsavia. Tuttavia, i polacchi erano già sicuri: il Reich sta giocando a un gioco che nel migliore dei casi mira a rendere vassallo il suo vicino. Hanno detto di no. - La Polonia non può essere spinta via dal Mar Baltico! - il capo del ministero degli Affari esteri polacco, Józef Beck, tuonò con un'esposizione. I preparativi per la guerra erano in pieno svolgimento. Nella primavera del 1939, lo stesso Hitler inviò un memorandum al governo di Varsavia. Tuttavia, i polacchi erano già sicuri: il Reich sta giocando a un gioco che nel migliore dei casi mira a rendere vassallo il suo vicino. Hanno detto di no. - La Polonia non può essere spinta via dal Mar Baltico! - tuonò il capo del ministero degli Affari esteri polacco, Józef Beck, con la denuncia del Sejm. I preparativi per la guerra erano in pieno svolgimento. Nella primavera del 1939, lo stesso Hitler inviò un memorandum al governo di Varsavia. Tuttavia, i polacchi erano già sicuri: il Reich sta giocando un gioco che nel migliore dei casi mira a rendere vassallo il suo vicino. Hanno detto di no. - La Polonia non può essere respinta dal Mar Baltico! - il capo del ministero degli Affari esteri polacco, Józef Beck, tuonò con un'esposizione. I preparativi per la guerra erano in pieno svolgimento.
        <br><br>Il piano per l'invasione tedesca era nome in codice Fall Weiss. La Wehrmacht doveva colpire da nord e da ovest, così come i territori di Boemia e Slovacchia controllati dal Reich. “Distruggere la Polonia è il nostro primo compito. L'obiettivo non deve essere quello di raggiungere una linea segnata, ma di distruggere una forza vivente. Sii spietato, sii brutale ", Hitler ha esortato i suoi comandanti durante una conferenza negli ultimi giorni di agosto, negoziando tranquillamente con l'Unione Sovietica, che doveva prestargli sostegno militare.<br><br>
        I militari polacchi, a loro volta, da marzo hanno lavorato febbrilmente al "Piano Operativo Ovest". Per loro, la prospettiva di una guerra con la Germania era qualcosa di nuovo. Per anni hanno considerato l'Armata Rossa una minaccia di gran lunga maggiore. Ora i pianificatori presumevano che la prima fase dei combattimenti si sarebbe svolta lungo i confini. Se l'aggressore non riesce a fermarsi, i polacchi si difenderanno lungo la linea della Vistola. La questione chiave in questo puzzle, tuttavia, era l'atteggiamento degli alleati. Le autorità polacche speravano che pochi giorni dopo l'aggressione tedesca, Francia e Regno Unito si unissero alla battaglia e la Wehrmacht, presa in difficoltà, si sarebbe arresa. A quel tempo, i decisori non sapevano che anche l'Armata Rossa si stava preparando ad attaccare la Polonia ... In estate, i tedeschi iniziarono a intensificare le attività sovversive. Hanno attaccato i posti di confine, organizzato un attentato dinamitardo alla stazione di Tarnów, ha tentato di occupare un tunnel strategico sul passo Jabłonkowska. Il culmine di tutta la serie di incidenti è stata la provocazione di Gliwice, cioè un raduno di un commando armato a una stazione radio, che si trovava ... sul lato tedesco del confine. I nazisti volevano incolpare i polacchi per il viaggio. Sebbene l'azione sia stata condotta in modo estremamente inetto, diverse dozzine di ore dopo Hitler la classificò come "casus belli". In linea con il suo credo, che ha esposto ai suoi comandanti alla suddetta conferenza: “Ai fini della propaganda, darò qualche motivo per lo scoppio della guerra, non importa se sarà credibile o meno. Nessuno chiede al vincitore se ha detto la verità o no. Nelle questioni relative all'avvio e allo svolgimento di una guerra il fattore decisivo non è la legge, ma la vittoria ”. cioè un'incursione di un commando armato a una stazione radio che si trovava ... sul lato tedesco del confine. I nazisti volevano incolpare i polacchi per il viaggio. Sebbene l'azione sia stata condotta in modo estremamente inetto, diverse dozzine di ore dopo Hitler la classificò come "casus belli". In linea con il suo credo, che ha esposto ai suoi comandanti alla suddetta conferenza: “Ai fini della propaganda, darò qualche motivo per lo scoppio della guerra, non importa se sarà credibile o meno. Nessuno chiede al vincitore se ha detto la verità o no. In materia di inizio e di guerra non è la legge che decide, ma la vittoria ”. cioè un'incursione di un commando armato a una stazione radio che si trovava ... sul lato tedesco del confine. I nazisti volevano incolpare i polacchi per il viaggio. Sebbene l'azione sia stata condotta in modo estremamente inetto, diverse dozzine di ore dopo Hitler la classificò come "casus belli". In linea con il suo credo, che ha esposto ai suoi comandanti alla suddetta conferenza: “Ai fini della propaganda, darò qualche motivo per lo scoppio della guerra, non importa se sarà credibile o meno. Nessuno chiede al vincitore se ha detto la verità o no. Nelle questioni relative all'avvio e allo svolgimento di una guerra il fattore decisivo non è la legge, ma la vittoria ”. Sebbene l'azione sia stata condotta in modo estremamente inetto, diverse dozzine di ore dopo Hitler la classificò come "casus belli". In linea con il suo credo, che ha esposto ai suoi comandanti alla suddetta conferenza: “Ai fini della propaganda, darò qualche motivo per lo scoppio della guerra, non importa se sarà credibile o meno. Nessuno chiede al vincitore se ha detto la verità o no. Nelle questioni relative all'avvio e allo svolgimento di una guerra, il fattore decisivo non è la legge, ma la vittoria ”. Sebbene l'azione sia stata condotta in modo estremamente inetto, diverse dozzine di ore dopo Hitler la classificò come "casus belli". In linea con il suo credo, che ha esposto ai suoi comandanti alla suddetta conferenza: “Ai fini della propaganda, darò qualche motivo per lo scoppio della guerra, non importa se sarà credibile o meno. Nessuno chiede al vincitore se ha detto la verità o no. In materia di inizio e di guerra non è la legge che decide, ma la vittoria”.
      </p>
      <h4 class="fw-bold"> Cinque settimane di combattimenti</h4>
      <p>25 sierpnia do portu w Gdańsku wpłynął pancernik „Schleswig-Holstein”. Oficjalnie jego załoga miała wziąć udział
        Il 25 agosto, la corazzata "Schleswig-Holstein" è entrata nel porto di Danzica. Ufficialmente, il suo equipaggio doveva prendere parte alle cerimonie di commemorazione dei marinai dell'incrociatore "Magdeburg", affondato durante la prima guerra mondiale. La nave, infatti, si stava preparando ad attaccare il deposito polacco di Westerplatte. Prima che venissero sparate le prime raffiche, tuttavia, i tedeschi hanno invitato a bordo i responsabili delle decisioni locali in conformità con una cerimonia diplomatica. “Un bicchiere di champagne con l'accompagnamento di sorrisi beffardi di ufficiali arroganti. È stato il momento più difficile della mia vita ”, ha osservato nel quotidiano Marian Chodacki, segretario generale della Repubblica di Polonia a Danzica. Il 1 settembre, alle 4:45, il rumore dei cannoni delle navi ha rotto il silenzio del mattino. La maggior parte degli storici sostiene che questo fu l'inizio della seconda guerra mondiale.<br><br>
        Per combattere contro il suo vicino orientale, i tedeschi hanno lanciato un totale di 1,8 milioni di soldati, 2,8 mila carri armati e 1,3 mila aerei. La Polonia potrebbe opporsi a meno di un milione di soldati, 880 carri armati e 400 aerei. La posizione strategica della Repubblica di Polonia era estremamente sfavorevole. I confini con i territori controllati dal Reich si estendevano per quasi tremila chilometri, inoltre, nella maggior parte dei luoghi non c'erano ostacoli naturali e fortificazioni. Le truppe della Wehrmacht mobili e pesantemente armate nei primi tre giorni hanno vinto la guerra di confine, rompendo le difese del nemico e spingendo i suoi eserciti in profondità nel paese. La speranza è stata riversata nei cuori dei polacchi dalla decisione dei governi di Francia e Gran Bretagna. Il 3 settembre, in linea con i loro impegni alleati, dichiararono guerra alla Germania. Tuttavia, l'attesa offensiva sul fronte occidentale non ha avuto luogo. Gli alleati decisero di non essere ancora pronti ad affrontare la macchina da guerra nazista. Tuttavia, non tutti i loro comandanti condividevano questa opinione. “Potresti entrare in posizioni tedesche come il burro. È stato possibile risolvere la guerra nel 1939. È bastato un po 'di determinazione e carattere - ha lamentato il maresciallo francese Alphonse Juin anni dopo. - Che vergogna, che vergogna, che stupidità allo stesso tempo. Questa è una situazione ideale, anche un modello, in cui potresti battere il tuo avversario andando alle sue spalle ". È bastato un po 'di determinazione e carattere - lamentò anni dopo il maresciallo francese Alphonse Juin. - Che vergogna, che vergogna, che stupidità allo stesso tempo. Questa è una situazione ideale, anche un modello, dove potresti battere il tuo avversario andando alle sue spalle ". È bastato un po 'di determinazione e carattere - ha lamentato il maresciallo francese Alphonse Juin anni dopo. - Che vergogna, che vergogna, che stupidità allo stesso tempo. Questa è una situazione ideale, anche un modello, in cui potresti battere il tuo avversario andando alle sue spalle".<br>
        Nel frattempo, nonostante il crescente vantaggio dell'avversario, i polacchi hanno lottato coraggiosamente. Nella notte tra il 9 e il 10 settembre sono persino riusciti a portare a termine uno sciopero efficace. Gli eserciti di "Poznań" e "Pomerania", comandati dal generale Tadeusz Kutreb, hanno inferto un colpo inaspettato all'ala dell'8a armata del generale Johannes Blaskowitz. La sanguinosa battaglia sulla Bzura fermò l'attacco tedesco a Varsavia per diverse dozzine di ore. Le speranze polacche furono finalmente sepolte il 17 settembre, quando l'Armata Rossa attaccò da est. Ad esempio, Varsavia cadde alla fine di settembre, la regione Fortificata di Hel si arrese il 2 ottobre e il Gruppo operativo indipendente Polesie comandato dal generale Franciszek Kleeberg quattro giorni dopo. La guerra difensiva è stata persa e la Polonia è scomparsa dalla mappa dell'Europa.</p>
      <h4 class="fw-bold">Questo è solo l'inizio...</h4>
      <p>Le perdite furono enormi. Molte città furono distrutte, molti villaggi furono dati alle fiamme. Tedeschi e sovietici dimostrarono che per raggiungere obiettivi militari e ideologici erano pronti a infrangere tutte le regole della guerra, i civili venivano regolarmente uccisi. Basta ricordare Wieluń: al momento del raid, non c'era un solo soldato in città, le truppe polacche erano di stanza a diverse decine di chilometri di distanza. Uno dei testimoni del raid ha ricordato “Il danno è stato enorme (…). Accanto all'ospedale, ho visto un cadavere appeso ai rami di un albero. Molti furono i morti e i feriti nelle strade (...). Il panico era indescrivibile. La gente fuggiva dalla città, molti correvano solo in mutande. La sorpresa è stata così grande che alcune persone sono impazzite. " 66.000 soldati polacchi furono uccisi nei combattimenti e quasi 700.000 furono fatti prigionieri. Ma i polacchi furono anche in grado di infliggere pesanti perdite agli attaccanti, in particolare ai tedeschi. Il Blitzkrieg pianificato dai comandanti nazisti si trasformò in un pesante combattimento durato oltre un mese. Durante la campagna di settembre, la Wehrmacht ha perso oltre 17.000 soldati, 217 carri armati, oltre diecimila diversi tipi di veicoli e enormi scorte di munizioni. Di conseguenza, hanno dovuto spostare l'invasione nell'Europa occidentale. Il compianto prof. Paweł Wieczorkiewicz ha osservato: "Hitler, valutando le conclusioni della campagna, ha affermato che" se la Polonia avesse armi anticarro, la marcia vittoriosa non sarebbe possibile ". In effetti, i cunei del serbatoio e l'aviazione hanno svolto un ruolo decisivo in questo. Il vantaggio materiale schiacciante della Wehrmacht (...) non era tanto la qualità dell'attrezzatura (...) quanto la sua quantità e la dottrina dell'uso massiccio ”. Secondo lo storico, durante i combattimenti difensivi, contrariamente alle opinioni diffuse in seguito, la piccola forza aerea e cavalleria polacca se la cavò abbastanza bene. “Mobile e ben equipaggiato con cannoni anticarro, era infatti il mezzo più efficace per fermare l'avanzata dei carri tedeschi. Nelle fasi successive della campagna, le unità di cavalleria furono in grado di eludere il nemico in modo più efficace e più lungo della fanteria che era fisicamente molto più stanca delle marce in ritirata.<br>
        Dopo aver sconfitto la Polonia, Hitler si rivolse alla Francia e alla Gran Bretagna con un'offerta di pace. Gli alleati, tuttavia, la respinsero. La guerra era appena iniziata e i soldati polacchi avrebbero segnato la loro presenza su quasi tutti i fronti.
      </p>
